      "elementdesc": "Com uma massa atómica de aproximadamente 1,0 u, o hidrogênio é o elemento menos denso. Ele geralmente apresenta-se em sua forma molecular, formando o gás diatômico (H2) nas condições normais de temperatura e pressão (CNTP).O hidrogênio é relativamente raro na Terra, e é industrialmente produzido a partir de hidrocarbonetos presentes no gás natural, tais como metano, após o qual a maior parte do hidrogênio elementar é usada \"em cativeiro\" ( no lugar de produção). Os maiores mercados do mundo usufruem do uso do hidrogênio para o aprimoramento de combustíveis fósseis (no processo de hidrocraqueamento) e na produção de amoníaco (maior parte para o mercado de fertilizantes). O hidrogênio também pode ser obtido por meio da eletrólise da água, porém, este processo é atualmente dispendioso, o que privilegia sua obtenção a partir do gás natural.Este gás é inflamável, incolor, inodoro, e insolúvel em água. O hidrogênio é previsto para ser o combustível ecológico do futuro.",
